import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Loader from '../../App/components/Reusables/Loader';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import { getDiscounts, deleteDiscounts } from '../../api/misc';
import DiscountsFilter from '../../App/components/SearchFilters/DiscountsFilter';
import { getDiscountsSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getStartEnd } from '../../helpers/getStartEnd';

const Discounts = (props) => {
    const [discountsArr, setDiscountsArr] = React.useState([]);
    const [loading, setLoading] = React.useState(true)
    const [deleteData, setDeleteData] = React.useState({ showModal: false, discountId: null })
    const [instanceCount, setInstanceCount] = React.useState(0)

    React.useEffect(() => {
        const controller = new AbortController();

        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getDiscountsSearchFilterObject(props),
            ...getStartEnd(props)
        }
        // console.log(JSON.stringify(requestJson))
        getDiscounts(requestJson, controller.signal)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setDiscountsArr(res.json.discounts);
                    setInstanceCount(res.json.count);
                    setLoading(false);
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });

        return () => controller.abort();
    }, []);


    const _triggerDeleteModal = (id) => {
        setDeleteData({ showModal: true, discountId: id });
    }

    const _delete = (idToBeDeleted) => {
        deleteDiscounts({ action: 'delete', discounts_id: [idToBeDeleted] })
            .then(async (res) => {
                console.log(res.json)
                if (res.json.status) {
                    const deletedDiscount = discountsArr.find(dis => dis.id === idToBeDeleted);
                    let tempArr = discountsArr;
                    tempArr.splice(tempArr.indexOf(deletedDiscount), 1);
                    await setDiscountsArr([...tempArr])
                    await setDeleteData({ ...setDeleteData, showModal: false, discountId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Discount deleted successfully!', background: '#dffff0' })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch((err) => {
                console.log(err)
            });
    }


    const DiscountsList = discountsArr.map((dis, index) => {
        return (
            <tr key={index}>
                <th scope="row">{dis.id}</th>
                <td
                    onClick={() => {
                        props.history.push('/discount/' + dis.id)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}>{dis.name}</td>
                <td>{dis.rate}</td>
                <td>{dis.discount_type}</td>
                <td>{dis.code}</td>
                <td>{dis.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-edit text-c-green f-19 m-r-10"
                        style={{ cursor: 'pointer' }}
                        onClick={() => props.history.push('/discount/update/' + dis.id)}
                    />
                    <i
                        className="feather icon-trash-2 text-c-red f-19 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeleteModal(dis.id)}
                    />
                </td>
            </tr>
        )
    })

    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Discounts</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`discount/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <DiscountsFilter />
                            <Table striped responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Name</th>
                                        <th>Rate</th>
                                        <th>Discount Type</th>
                                        <th>Discount Code</th>
                                        <th>Enabled</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {DiscountsList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(discountsArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >


                    <DeleteModal
                        show={deleteData.showModal}
                        onCancel={() => setDeleteData({ ...deleteData, showModal: false })}
                        onDelete={() => _delete(deleteData.discountId)}
                    />


                </Col>
            </Row>
        </Aux>
    );
}

export default Discounts;